<template>
  <div class="home">
    <div v-if="IS_CONNECTED">
      <div class="home__inputs">
        <div class="input-group mb-3">
          <span class="input-group-text" id="inputGroup-sizing-default">Metric Name</span>
          <input v-model="metricName"  placeholder="metric1, metric2, metric3..." type="text" class="form-control" aria-describedby="inputGroup-sizing-default">
        </div>
        <div class="input-group mb-3">
          <span class="input-group-text" id="inputGroup-sizing-default">Description</span>
          <input v-model="description" type="text" class="form-control" aria-describedby="inputGroup-sizing-default">
        </div>
        <div class="home__radios">
          <div class="form-check">
            <input class="form-check-input" type="radio" name="exampleRadios" id="exampleRadios1" value="0" v-model="type">
            <label class="form-check-label" for="exampleRadios1">
              Oracle Metric
            </label>
          </div>
          <div class="form-check">
            <input class="form-check-input" type="radio" name="exampleRadios" id="exampleRadios2" value="1" v-model="type">
            <label class="form-check-label" for="exampleRadios2">
              Oracle KYC
            </label>
          </div>
        </div>
        <button @click="handleBecomeOracle" type="button" class="btn btn-primary">Become Oracle</button>
      </div>
      <button @click="GET_ORACLE_PROPOSALS" type="button" style="margin-top: 20px;" class="btn btn-outline-warning">Update cards</button>
      <div class="home__cards">
        <Oracle :proposal="proposal" v-for="proposal, index in ORACLE_PROPOSALS" :key="index" />
      </div>
    </div>
    <div v-else class="mx-auto">
      <button @click="CONNECT_METAMASK" type="button" class="btn btn-warning btn-lg">Connect Wallet</button>
    </div>
    <Window/>
    <Loader v-if="LOADER" />
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import Loader from "../components/Loader";
import Oracle from "../components/Oracle.vue";
import Window from "../components/Window.vue";

export default {
  data() {
        return {
          type: 0,
          metricName: '',
          description: '',
        }
    },
  name: 'Main',
  async created() {
      await this.GET_ORACLE_PROPOSALS();
  },
  components: {
    Loader,
    Oracle,
    Window
  },
  computed: {
    ...mapGetters(['IS_CONNECTED','LOADER','ORACLE_PROPOSALS'])
  },
  methods: {
    ...mapActions(['CONNECT_METAMASK','GET_ORACLE_PROPOSALS','BECOME_ORACLE']),
    handleBecomeOracle() {
      if(!this.description) {
        this.$vToastify.info('Fill description', 'Validation warning')
      } else if (!this.metricName) {
        this.$vToastify.info('Enter the names of the metrics separated by commas', 'Validation warning')
      } else {
        let metricsArray = this.metricName.split(",");
        metricsArray.forEach(function callback(currentValue, index, array) {
          array[index] = currentValue.trim();
        })
        const result = metricsArray.filter(word => word);
        if(result.length > 1 && this.type == 1) {
          this.$vToastify.info('KYC oracle can have only one name', 'Validation warning')
        } else {
          const data = { metricName: result, description: this.description, type: this.type };
          console.log('data', data);
          this.BECOME_ORACLE(data);
        }
      }
    }
  },
}
</script>

