.home__inputs {
  width: 900px;
  margin: 0 auto;
}
.home__cards {
  width: 900px;
  margin: 20px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.home__radios {
  display: flex;
  justify-content: space-between;
  margin: 20px auto;
  width: 600px;
} 
.card {
  margin-bottom: 30px;
}
</style>
